				"html": "<p>Insertion sort is a <a href=\"https://en.wikipedia.org/wiki/Comparison_sort\"><em>comparison</em> based sorting</a>. A sorting algorithm is comparison based, if it uses comparison operators (such as <code>less than</code> and <code>greated than</code>) to find the order between two numbers.</p>\n<p>In this sorting technique, we always maintain a sorted sublist in lower position of list and then we take one element from the rest of list and insert it at it's correct place. We does so untill all of the elements are inserted into sublist. For example, while playing cards we sort cards in our hand. Starting from left and moving to right, we keep on inserting the card at it's right place till end.</p>\n<h2>Example</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/289cddf207e54981a05b56d9c267d078ed827c8b.png\" alt=\"Insertion Sort\"></p>\n<p>In the above example, the <code>grey shaded</code> sublist is always sorted. Please note that in the beginning, sublist contains ony one element, and <em>trivially</em> sorted. Then at each step we are inserting the leftmost element of <code>white shaded</code> sublist at it's correct position.</p>\n<p>Hence, we have sorted the complete list in this way.</p>\n<h2>Algorithm</h2>\n<pre><code>Loop for i=0 to N-1:\n* Pick element array[i] and insert it into sorted sublist array[0...i-1]\n</code></pre>\n<h2>Complexity</h2>\n<pre><code>Space complexity: O(1)      // Auxillary/temporary space is used.\n\nTime complexity: O(n*n)     // Two nested for loops are used.\n</code></pre>\n<h2>C++ Implementation</h2>\n<pre><code>// Function to sort an array using insertion sort\nvoid insertionSort(int arr[], int n)\n{\n   int i, key, j;\n   for (i = 1; i &#x3C; n; i++)\n   {\n       key = arr[i];\n       j = i-1;\n\n       /* Move elements of arr[0..i-1], that are greater than key,\n      to one position ahead of their current position */\n   while (j >= 0 &#x26;&#x26; arr[j] > key)\n   {\n       arr[j+1] = arr[j];\n       j = j-1;\n   }\n   arr[j+1] = key; // place element key at it's correct place\n   }\n}\n\nint main()\n{\n    // array to be sorted\n    int arr[5] = {12, 11, 13, 5, 6};\n\n    // call the insertion sort\ninsertionSort(arr, 5);\n\n// prints sorted array i.e. 5 6 11 12 13\nfor(int i=0; i&#x3C;5; i++)\n    std::cout &#x3C;&#x3C; arr[i] &#x3C;&#x3C; \" \";\nreturn 0;\n}\n</code></pre>\n<p>:rocket: <a href=\"https://repl.it/CWZq\">Run Code</a> ## Python Implementation  </p>\n<pre><code># Function to perform insertion sort\ndef insertionSort(arr):\n    # Traverse through array\n    for i in range(1, len(arr)):\n        key = arr[i]\n        # Move elements of arr[0..i-1], that are greater than key,\n        # to one position ahead of their current position\n        j = i-1\n        while j>=0 and key &#x3C; arr[j] :\n                arr[j+1] = arr[j]\n                j -= 1\n        arr[j+1] = key # place element key at it's correct place\n\n# array to be sorted\narr = [12, 11, 13, 5, 6]\n# call the insertion sort\ninsertionSort(arr)\n# prints sorted array i.e. 5 6 11 12 13\nfor i in range(len(arr)):\n    print(arr[i],end = ' ')\n</code></pre>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CWZi\">Run Code</a></p>\n<h2>Advantages</h2>\n<ol>\n<li>Efficient for small set of data and data set that are almost sorted.</li>\n<li>Simply implemented.</li>\n<li>Mostly better than bubble sort and selection sort &#x26; generally used with merge sort.</li>\n</ol>\n<h2>Disadvantages</h2>\n<ol>\n<li>It's less efficient on large set of data than merge sort, heap sort and quick sort.</li>\n</ol>",
